import { h, render, Component } from 'preact';

import { config } from './cta.config.js'

import style from './style.scss';

const axios = require('axios');

// Map object representing the CTA defined train line keys and their respective names
const CTA_ROUTES = {
	red: 'Red',
	blue: 'Blue',
	brn: 'Brown',
	g: 'Green',
	org: 'Orange',
	p: 'Purple',
	pink: 'Pink',
	y: 'Yellow'
};

class CtaAppTrain extends Component {

	state = {
		destination: '',
		predictions: '',
		error: ''
	};

	componentDidMount() {
		this.getNextTransit(this.props.stopId, this.props.route);
	}

	// Given a prdt and a arrT, return an integer representing the minutes difference between the two timestamps
	getDifferenceInMinutes = (arrT, prdt) => {
		return ((new Date(arrT)) - (new Date(prdt))) / 60000;
	};

	// Given the raw countdown (in minutes) from the CTA API, return the modified/filtered string for display
	getModifiedCountdown = countdown => {
		if(isNaN(countdown)){
			// TODO ERROR
		}else if(countdown <= 1){
			return "Due";
		}else{
			return countdown + " min";
		}
	};

	// Given the stop id, return a string representing the next transit(s) predicted
	getNextTransit = async (stopId, route) => {

		let response = await axios.get(`${config.PROXY_URL}${config.TRAIN_ROOT_URL}api/1.0/ttarrivals.aspx?key=${config.TRAIN_API_KEY}&stpid=${stopId}&outputType=JSON`);

		if(response && response.data && response.data.ctatt){

			if(response.data.ctatt.eta && 0 < response.data.ctatt.eta.length){

				console.log(response.data.ctatt.eta);

				// Filter on the given route predictions to ensure all predictions belong to the current route/train line focused on
				let predictions = response.data.ctatt.eta.filter(prediction => prediction.rt.toLowerCase() === route.toLowerCase());
				if(0 < predictions.length){

					let predictionValues = [];
					predictions.forEach((prediction, index) => {
						predictionValues.push(this.getModifiedCountdown(this.getDifferenceInMinutes(prediction.arrT, prediction.prdt)));
					});

					this.setState({
						stationName: predictions[0].staNm,
						destination: predictions[0].destNm,
						predictions: predictionValues
					});

				}else{
					// The CTA Train Stop has predictions for other train routes, but not this train route
					this.setState({
						error: (CTA_ROUTES[route.toLowerCase()] + " Line: No arrival times")
					});
				}

			}else if (response.data.ctatt.errNm && 0 < response.data.ctatt.errNm.length){
				this.setState({
					error: (CTA_ROUTES[route.toLowerCase()] + " Line: " + response.data.ctatt.errNm)
				});
			}else{
				// TODO ERROR
			}

		}else{
			// TODO ERROR
		}
	};

	render() {

		if(0 < this.state.predictions.length){

			let predictionsStr = '';
			this.state.predictions.forEach((prediction, index) => {
				if(index !== 0){
					predictionsStr += prediction;
					if(index !== (this.state.predictions.length - 1)){
						predictionsStr += ', ';
					}
				}
			});

			const ctaRouteColorStyles = {
				Red: style.ctaAppChildTrainRed,
				Blue: style.ctaAppChildTrainBlue,
				Brown: style.ctaAppChildTrainBrown,
				Green: style.ctaAppChildTrainGreen,
				Orange: style.ctaAppChildTrainOrange,
				Purple: style.ctaAppChildTrainPurple,
				Pink: style.ctaAppChildTrainPink,
				Yellow: style.ctaAppChildTrainYellow
			};

			return (

				<div className={[style.ctaAppChild, style.ctaAppChildTrain, ctaRouteColorStyles[CTA_ROUTES[this.props.route.toLowerCase()]]].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.destination}
						</div>
						<div className={style.ctaAppChildSmall}>
							{this.state.stationName} {CTA_ROUTES[this.props.route.toLowerCase()]} Line
						</div>
					</div>
					<div className={style.ctaAppChildRight}>
						<div className={style.ctaAppChildLarge}>
							{this.state.predictions[0]}
						</div>
						<div className={style.ctaAppChildSmall}>
							{predictionsStr}
						</div>
					</div>
				</div>

			);
		}else if (0 < this.state.error.length){
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildTrain].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.error}
						</div>
					</div>
				</div>
			);
		}else {
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildTrain].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							Loading {CTA_ROUTES[this.props.route.toLowerCase()]} Line
						</div>
					</div>
				</div>
			);
		}
	}
}

class CtaApp extends Component {

	state = {
		direction: '',
		destination: '',
		predictions: '',
		error: ''
	};

	componentDidMount() {
		this.getNextTransit(this.props.route, this.props.stopId);
	}

	// Given the raw direction from the CTA API, return the modified/filtered string for display
	getModifiedDirection = direction => {
		return direction.replace('bound', '');
	};

	// Given the raw countdown string from the CTA API, return the modified/filtered string for display
	getModifiedCountdown = countdown => {
		if(isNaN(countdown)){
			if(countdown.toUpperCase().includes('DUE')){
				return 'Due';
			}else if(countdown.toUpperCase().includes('DLY')){
				return 'Delayed';
			}else{
				return countdown;
			}
		}else{
			return countdown + " min"
		}
	};

	// Given the route and stop number, return a string representing the next transit(s) predicted
	getNextTransit = async (route, stopId) => {

		let response = await axios.get(`${config.PROXY_URL}${config.BUS_ROOT_URL}bustime/api/v2/getpredictions?key=${config.BUS_API_KEY}&format=json&rt=${route}&stpid=${stopId}`);

		// TODO Handle response.data['bustime-response'].error -> "msg": "No arrival times"
		if(response && response.data && response.data['bustime-response']){
			if(response.data['bustime-response'].prd && 0 < response.data['bustime-response'].prd.length){

				console.log(response);

				let predictionValues = [];
				let predictions = response.data['bustime-response'].prd;
				predictions.forEach((prediction, index) => {
					predictionValues.push(this.getModifiedCountdown(prediction.prdctdn));
				});

				this.setState({
					direction: this.getModifiedDirection(response.data['bustime-response'].prd[0].rtdir),
					destination: response.data['bustime-response'].prd[0].des,
					predictions: predictionValues
				});

			}else if (response.data['bustime-response'].error && 0 < response.data['bustime-response'].error.length){
				this.setState({
					error: (this.props.route + ": " + response.data['bustime-response'].error[0].msg)
				});
			}else{
				// TODO ERROR
			}
		}else{
			// TODO ERROR
		}
	};

	render() {

		if(0 < this.state.predictions.length){

			let predictionsStr = '';
			this.state.predictions.forEach((prediction, index) => {
				if(index !== 0){
					predictionsStr += prediction;
					if(index !== (this.state.predictions.length - 1)){
						predictionsStr += ', ';
					}
				}
			});

			return (

				<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.props.route} to {this.state.destination}
						</div>
						<div className={style.ctaAppChildSmall}>
							{this.state.direction}
						</div>
					</div>
					<div className={style.ctaAppChildRight}>
						<div className={style.ctaAppChildLarge}>
							{this.state.predictions[0]}
						</div>
						<div className={style.ctaAppChildSmall}>
							{predictionsStr}
						</div>
					</div>
				</div>

			);
		}else if (0 < this.state.error.length){
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.error}
						</div>
					</div>
				</div>
			);
		}else {
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							Loading {this.props.route} at {this.props.stopId}
						</div>
					</div>
				</div>
			);
		}
	}
}

export default class CtaAppParent extends Component {

	state = {
		showTrains: true
	};

	showTrains = () => {
		this.setState(prev => ({showTrains: true}));
	};

	showBuses = () => {
		this.setState(prev => ({showTrains: false}));
	};

	render() {

		let trainStops = [];
		let busStops = [];
		config.STOPS.forEach((stop) => {
			if(stop.isTrain){
				trainStops.push(<CtaAppTrain route={stop.route} stopId={stop.stopId} />);
			}else{
				busStops.push(<CtaApp route={stop.route} stopId={stop.stopId} />);
			}
		});

		return (
			<div className={[style.ctaApp, (this.state.showTrains ? style.ctaAppShowTrains : style.ctaAppShowBuses)].join(' ')}>

				<div className={style.ctaAppControls}>

					<button onClick={this.showTrains} role={'button'} aria-label={'Show Train Times'} aria-pressed={this.state.showTrains} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnTrain, (this.state.showTrains ? style.ctaAppControlsBtnActive : '')].join(' ')}>Show Trains</button>

					<button onClick={this.showBuses} role={'button'} aria-label={'Show Bus Times'} aria-pressed={!this.state.showTrains} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnBus, (this.state.showTrains ? '' : style.ctaAppControlsBtnActive)].join(' ')}>Show Buses</button>

					<button role={'button'} aria-label={'Show Bus Times'} aria-pressed={'true'} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnRefresh].join(' ')}>Refresh</button>

				</div>

				<div className={[style.ctaAppStops, style.ctaAppStopsTrains].join(' ')}>
					{trainStops}
				</div>

				<div className={[style.ctaAppStops, style.ctaAppStopsBuses].join(' ')}>
					{busStops}
				</div>

			</div>
		);
	}
}